___CSS_LOADER_EXPORT___.push([module.id, `.login-page {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh; 
    text-align: center; 
    padding: 0 20px; 
    font-family: 'DM Sans', sans-serif;
}

.login-page h1 {
    font-size: 2.5rem;
    color: #333; 
    margin-bottom: 30px; 
}

.login-with-google {
    display: flex;
    align-items: center; 
    background-color: #4285F4; 
    color: #ffffff; 
    border: none;
    border-radius: 4px;
    padding: 10px 20px; 
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s ease; 
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); 
}

.google-image {
    width: 24px; 
    height: 24px; 
    margin-right: 10px; 
}

.login-with-google:hover {
    background-color: #357ae8; 
}

.login-with-google:focus {
    outline: none; 
}

.login-with-google:active {
    background-color: #3367d6; 
}`, "",{"version":3,"sources":["webpack://./src/style/login.css"],"names":[],"mappings":"AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,aAAa;IACb,kBAAkB;IAClB,eAAe;IACf,kCAAkC;AACtC;;AAEA;IACI,iBAAiB;IACjB,WAAW;IACX,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,yBAAyB;IACzB,cAAc;IACd,YAAY;IACZ,kBAAkB;IAClB,kBAAkB;IAClB,eAAe;IACf,iBAAiB;IACjB,eAAe;IACf,sCAAsC;IACtC,wCAAwC;AAC5C;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,yBAAyB;AAC7B","sourcesContent":["@import url(\"https://fonts.googleapis.com/css?family=DM+Sans:400,500,700&display=swap\");\r\n\r\n.login-page {\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n    height: 100vh; \r\n    text-align: center; \r\n    padding: 0 20px; \r\n    font-family: 'DM Sans', sans-serif;\r\n}\r\n\r\n.login-page h1 {\r\n    font-size: 2.5rem;\r\n    color: #333; \r\n    margin-bottom: 30px; \r\n}\r\n\r\n.login-with-google {\r\n    display: flex;\r\n    align-items: center; \r\n    background-color: #4285F4; \r\n    color: #ffffff; \r\n    border: none;\r\n    border-radius: 4px;\r\n    padding: 10px 20px; \r\n    font-size: 16px;\r\n    font-weight: bold;\r\n    cursor: pointer;\r\n    transition: background-color 0.3s ease; \r\n    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); \r\n}\r\n\r\n.google-image {\r\n    width: 24px; \r\n    height: 24px; \r\n    margin-right: 10px; \r\n}\r\n\r\n.login-with-google:hover {\r\n    background-color: #357ae8; \r\n}\r\n\r\n.login-with-google:focus {\r\n    outline: none; \r\n}\r\n\r\n.login-with-google:active {\r\n    background-color: #3367d6; \r\n}"],"sourceRoot":""}]);
