    padding: 10px 10px;
    font-family: 'DM Sans', sans-serif;
    font-size: 16px;
    line-height: 24px;
    font-weight: 700;
    background-color: #f3f6fd;
}

nav {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content:space-between;
    align-items: center;
}

.right-side ul {
    list-style: none;
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
    margin-right: 10px;
}

.right-side ul li img {
    width: 28px;
    height: 28px;
    margin-right: 10px;
    margin-top: 5px;
}

.user-details{
    padding: 0;
    border: 0;
    background-color: transparent;
    display: flex;
    align-items: center;
    padding-left: 12px;
    border-left: 2px solid #ddd;
}

.user-image img{
    border-radius: 50%;
    margin-right: 4px;
}

.right-side li:not(:last-child) {
    margin:0;
    padding:0;
}

.left-side{
    margin-left: 10px;
    font-size: 25px;
