___CSS_LOADER_EXPORT___.push([module.id, `.class-form{
    font-family: 'DM Sans', sans-serif;
    padding: 10px 30px;
    padding-bottom: 50px;
    max-width:30%;
    /* margin:40px auto; */
    border-radius:4px;
    box-shadow:0 4px 10px 4px rgba(#13232f,.3);
    border: 1px solid black;
    background-color: white;
}

.form-group{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
}

label {
    flex: 1; /* Make labels take up equal space */
    text-align: left;
    margin-right: 10px;
}

input[type="text"],
input[type="date"],
input[type="time"]{
    flex: 2; /* Make inputs take up twice as much space as labels */
    padding: 5px;
    box-sizing: border-box; /* Ensure padding doesn't affect width */
}

input[type="checkbox"] {
    width: 20px;
    height: 20px;
}

button[type="submit"] {
    padding: 10px;
    background-color: #007bff;
    color: white;
    border: none;
    cursor: pointer;
    float:right;
    border-radius: 10px;
    font-family: 'DM Sans', sans-serif;
}

button[type="submit"]:hover {
    background-color: #0056b3;
